@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
/* Roboto */ /* Open Sans */

html, body, #root, .App {
  height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.App > div {
  overflow: hidden;
}
.SignOutButton {
  border: none;
  display: block;
  overflow: hidden;
  background-repeat: no-repeat;
  background-color: transparent;

  padding: 30px;

  color: white;
  cursor: pointer;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}
.Navigation {
  background-color: #333333;
}

ul {
  list-style-type: none;

  margin: 0;
  padding: 0;
}

li {
  float: left;
}

li * {
  display: block;
  padding: 30px;
  color: white;
  text-align: center;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}

.SignOutButton {
  margin-left: auto;
}
.TagList {
  display: flex;
  flex-direction: column;

  border: 2px solid black;

  padding: 20px;
}

.TagList > input {
  padding: 10px;

  border-radius: 10px;
}
.Editor {
  display: flex;

  height: 100%;
}

.VideoArea {  
  flex-grow: 1;
}

.TagList {
  width: 20%;
  min-width: 200px;
}

